@import "../../style.scss";

.rightBar{
    @include themify($themes){
    flex: 3;
    position: sticky;
    top: 50px;
    height: calc(100vh - 50px);
    overflow: scroll;
    background-color: themed("bgSoft");

    @include mobile{
        display: none;
      }

        
    @include tablet{
            display: none;
    }

    &::-webkit-scrollbar{
        display: none;
    }
    .container{
        padding:20px;

        .item{
            -webkit-box-shadow: 0px 0px 25px -10px rgba(0, 0, 0, 0.38);
            -moz-box-shadow: 0px 0px 25px -10px rgba(0, 0, 0, 0.38);
            box-shadow: 0px 0px 25px -10px rgba(0, 0, 0, 0.38);
            padding: 20px;
            margin-bottom: 20px;
            background-color: themed("bg");


            span{
                color:gray;
            }

            .user{
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin: 20px 0px;
                .userInfo{
                    display: flex;
                    align-items: center;
                    gap: 20px;
                    position: relative;
                    color: themed("textColorSoft");

                    img { 
                        width: 40px;
                        height: 40px;
                        border-radius: 50%;
                        object-fit: cover;
                    }

                    .online{
                        width: 12px;
                        height: 12px;
                        border-radius: 50%;
                        background-color: limegreen;
                        position: absolute;
                        top: 0;
                        left: 30px;

                    }
                    p{
                        color: themed("textColorsoft");
                    }

                    span{
                        font-weight: 500;
                        color: themed("textColor");
                    }
                }
                .buttons{
                    display: flex;
                    flex-wrap: wrap;
                    align-items: center;
                    gap: 10px;
                    
                    button{
                        border: none;
                        padding: 5px;
                        color: white;
                        cursor: pointer;

                        &:first-child{
                            background-color: #5271ff;
                        }

                        &:last-child{
                            background-color: #f0544f;
                        }
                    }
                }
            }
        }
    }
}
}